.main {
  background-color: rgb(28, 30, 37); 
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.menu {
  font-size: 30px;
  width: 100%;
  display: table;
  text-align: center;
  /* background-color: #282c34;
  box-shadow: 0px 8px 4px #282c34; */

}

.nav-link {
  display: inline-block;
  margin: 20px 45px;
  margin-top: 30px;
  text-decoration: none;
  color: whitesmoke;
}

.active {
  display: inline-block;
  margin: 20px 45px;
  margin-top: 30px;
  text-decoration: none;
  color: whitesmoke;
  text-decoration: underline #039761;
}


.banner-text {
  position: absolute;
  bottom: 0;
 display: block;
  min-width: 100%;
  height: auto;
  font-size: 13px;
}